import React, { useEffect, useState } from "react";
import { Modal, Fab, makeStyles } from "@material-ui/core";
import ExitToAppOutlinedIcon from "@material-ui/icons/ExitToAppOutlined";
import AddIcon from "@material-ui/icons/Add";
import StockTable from "../components/StockTable";
import "./Main.css";
import AddStock from "../components/AddStock";
import { auth, db } from "../utils/firebase";
import { connect } from "react-redux";
import { logout } from "../store/Store";

const useStyles = makeStyles(() => ({
  fav: {
    position: "fixed",
    bottom: 0,
    left: "50%",
    transform: "translateX(-50%)",
    marginBottom: "30px",
  },
}));

function Main({ history, store, doLogout }) {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [tradings, setTradings] = useState([]);
  const { authenticate } = store;

  if (!authenticate.isAuth) {
    history.replace("/");
  }

  useEffect(() => {
    const unsubscribe = db
      .collection("user")
      .doc(authenticate?.user)
      .collection("trading")
      .orderBy("priority", "asc")
      .onSnapshot((snapshot) => {
        setTradings(
          snapshot.docs.map((doc) => ({
            key: doc.id,
            data: doc.data(),
          }))
        );
      });
    return unsubscribe;
  }, [authenticate]);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleLogout = () => {
    auth.signOut().then(() => {
      doLogout(false);
    });
  };

  return (
    <>
      <header>
        <div className="header_main">
          <span className="username">{authenticate.user}</span>
          <div className="logout" onClick={handleLogout}>
            <span>Logout</span>
            <ExitToAppOutlinedIcon />
          </div>
        </div>
      </header>
      <div className="main">
        {tradings.map((trading) => (
          <StockTable key={trading.key} trading={trading} />
        ))}
      </div>
      <Fab
        className={classes.fav}
        color="primary"
        aria-label="add"
        onClick={handleOpen}
      >
        <AddIcon />
      </Fab>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="add-stock"
        aria-describedby="add-stock"
      >
        <div>
          <AddStock setOpen={setOpen} />
        </div>
      </Modal>
    </>
  );
}

function mapStateToProps(state) {
  return { store: state };
}

function mapDispatchToProps(dispatch) {
  return {
    doLogout: () => dispatch(logout()),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Main);
